.card-body h1,
h2 {
    font-size: 18px;
    margin-top: 10px;
}

a{
    text-decoration: none;
}
.card-body div {
    display: flex;
    justify-content: space-between;
    border-bottom: solid 1px #000000;
}

.card-body div p {
    margin: 0px;
}

.card {
   
    color: black;
    border: none;
}

.network-logos{
    border-radius: 60%;
    width: 40%;
    padding: 26px 20px;
    background-color: #a1a1a1;
}